import moment from 'moment';

export default class MomentUtil {
  public static toDateMoment(datePoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  }): any {
    // YYYY-4-31 などが指定されたときに YYYY-4-30 に修正する
    let endDayOfMonth: number = moment(new Date(datePoint.year, datePoint.month, 1))
      .endOf('month').date();
    let datePointDay: number = datePoint.day;
    if (endDayOfMonth < datePoint.day) {
      datePointDay = endDayOfMonth;
    }
    return moment(new Date(
      datePoint.year,
      datePoint.month,
      datePointDay,
      datePoint.hour,
      datePoint.minute,
      datePoint.second,
      datePoint.millisecond,
    ));
  }

  public static toDatePoint(dateMoment: any): {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } {
    return {
      year: dateMoment.year(),
      month: dateMoment.month(),
      day: dateMoment.date(),
      hour: dateMoment.hour(),
      minute: dateMoment.minutes(),
      second: dateMoment.second(),
      millisecond: dateMoment.millisecond(),
    };
  }

  public static diffDatePointsToTotalPointDiff(
    startDatePoint: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    },
    endDatePoint: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    },
    thresholdUnit: string = 'year'): {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } {
    let startDateMoment: any = MomentUtil.toDateMoment(startDatePoint);
    let endDateMoment: any = MomentUtil.toDateMoment(endDatePoint);
    return MomentUtil.diffDateMomentsToTotalPointDiff(startDateMoment, endDateMoment, thresholdUnit);
  }

  public static diffDateMomentsToTotalPointDiff(
    startDateMoment: any,
    endDateMoment: any,
    thresholdUnit: string = 'year'): {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } {
    let totalPointDiff: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    } = {
      year: 0,
      month: 0,
      day: 0,
      hour: 0,
      minute: 0,
      second: 0,
      millisecond: 0,
    };
    let startDateMomentCloned: any = startDateMoment.clone();
    let endDateMomentCloned: any = endDateMoment.clone();
    if (0 < endDateMomentCloned.diff(startDateMomentCloned)) {
      switch(thresholdUnit) {
        case 'year':
          totalPointDiff.year = endDateMomentCloned.diff(startDateMomentCloned, 'years');
          startDateMomentCloned.add(totalPointDiff.year, 'years');
        case 'month':
          totalPointDiff.month = endDateMomentCloned.diff(startDateMomentCloned, 'months');
          startDateMomentCloned.add(totalPointDiff.month, 'months');
        case 'day':
          totalPointDiff.day = endDateMomentCloned.diff(startDateMomentCloned, 'days');
          startDateMomentCloned.add(totalPointDiff.day, 'days');
        case 'hour':
          totalPointDiff.hour = endDateMomentCloned.diff(startDateMomentCloned, 'hours');
          startDateMomentCloned.add(totalPointDiff.hour, 'hours');
        case 'minute':
          totalPointDiff.minute = endDateMomentCloned.diff(startDateMomentCloned, 'minutes');
          startDateMomentCloned.add(totalPointDiff.minute, 'minutes');
        case 'second':
          totalPointDiff.second = endDateMomentCloned.diff(startDateMomentCloned, 'seconds');
          startDateMomentCloned.add(totalPointDiff.second, 'seconds');
        default:
          totalPointDiff.millisecond = endDateMomentCloned.diff(startDateMomentCloned, 'milliseconds');
      }
    }
    return totalPointDiff;
  }

  public static diffDatePointsToEachPointDiff(
    startDatePoint: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    },
    endDatePoint: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    }): {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } {
    let startDateMoment: any = MomentUtil.toDateMoment(startDatePoint);
    let endDateMoment: any = MomentUtil.toDateMoment(endDatePoint);
    return MomentUtil.diffDateMomentsToEachPointDiff(startDateMoment, endDateMoment);
  }

  public static diffDateMomentsToEachPointDiff(
    startDateMoment: any,
    endDateMoment: any): {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
    millisecond: number,
  } {
    let eachPointDiff: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    } = {
      year: 0,
      month: 0,
      day: 0,
      hour: 0,
      minute: 0,
      second: 0,
      millisecond: 0,
    };
    let startDateMomentCloned: any = startDateMoment.clone();
    let endDateMomentCloned: any = endDateMoment.clone();
    if (0 < endDateMomentCloned.diff(startDateMomentCloned)) {
      eachPointDiff.year = endDateMomentCloned.diff(startDateMomentCloned, 'years');
      eachPointDiff.month = endDateMomentCloned.diff(startDateMomentCloned, 'months');
      eachPointDiff.day = endDateMomentCloned.diff(startDateMomentCloned, 'days');
      eachPointDiff.hour = endDateMomentCloned.diff(startDateMomentCloned, 'hours');
      eachPointDiff.minute = endDateMomentCloned.diff(startDateMomentCloned, 'minutes');
      eachPointDiff.second = endDateMomentCloned.diff(startDateMomentCloned, 'seconds');
      eachPointDiff.millisecond = endDateMomentCloned.diff(startDateMomentCloned, 'milliseconds');
    }
    return eachPointDiff;
  }

  public static addPointDiffToDateMoment(
    pointDiff: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    }, dateMoment: any): any {
    let momentDiff: any = dateMoment.clone();
    momentDiff.add(pointDiff.year, 'years');
    momentDiff.add(pointDiff.month, 'months');
    momentDiff.add(pointDiff.day, 'days');
    momentDiff.add(pointDiff.hour, 'hours');
    momentDiff.add(pointDiff.minute, 'minutes');
    momentDiff.add(pointDiff.second, 'seconds');
    momentDiff.add(pointDiff.millisecond, 'milliseconds');
    return momentDiff;
  }

  public static subtractPointDiffToDateMoment(
    pointDiff: {
      year: number,
      month: number,
      day: number,
      hour: number,
      minute: number,
      second: number,
      millisecond: number,
    }, dateMoment: any): any {
    let momentDiff: any = dateMoment.clone();
    momentDiff.subtract(pointDiff.year, 'years');
    momentDiff.subtract(pointDiff.month, 'months');
    momentDiff.subtract(pointDiff.day, 'days');
    momentDiff.subtract(pointDiff.hour, 'hours');
    momentDiff.subtract(pointDiff.minute, 'minutes');
    momentDiff.subtract(pointDiff.second, 'seconds');
    momentDiff.subtract(pointDiff.millisecond, 'milliseconds');
    return momentDiff;
  }
}